import Head from 'next/head'

const Meta = () => (
  <div>
    <Head>
        <meta name="viewport" content="width=device-width, initial-scale=1, shrink-to-fit=no" />
        <meta charSet="utf-8" />
        <link href="https://fonts.googleapis.com/css?family=Roboto:400,500,700" rel="stylesheet" />
    </Head>
    <style jsx global>{`
        * {
            margin: 0;
            padding: 0;
            font-family: "Roboto";
            color: #333;
        }

        body { 
            background: #ededed;
        }
        
        a {
            text-decoration: none;
            color: #722f37;
        }

        a:hover {
            color: #a5434f;
        }
        
        button {
            font-weight: 600;
            text-transform: uppercase;
            padding: 0.6em 0.8em;
            border: 0;
            margin-right: 0.5em;
            margin-top: 1em;
        }

        button:hover {
            cursor: pointer;
        }

        button.delete {
            background: #f45342;
        }

        input[type=text] {
            padding: 0.5em;
            border: 1px solid rgba(0, 0, 0, 0.1);
        }
    `}</style>
  </div>
)

export default Meta 


